.header {
  background-color: #7ea3dd;
  height: 80px;
  position: fixed;
  width: 100%;
  top:0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.header__plus{
  background-image: url('./plus2.svg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 50px;
  height: 0px;
  padding-top: 50px;
  overflow: hidden;
}



