.editor__buttons{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.link{
  text-decoration: none;
}
.button{
  margin: 10px;
  display: block;
  width: 200px;
  height: 40px;
  background-color: #7ea3dd;
  border: none;
  border-radius: 5px;
  font-size: 20px;
  font-weight: bold;
  cursor: pointer;
}
.button:hover{
  width: 210px;
  height: 50px;
  background-color: #526b91;
  color: white;
}

.button--cancel{
  background-color: #eaecef;
  color: #77797a;
  font-size: 18px;
}
.button--cancel:hover{
  width: 200px;
  height: 40px;
  background-color: #eaecef;
  color: #77797a;
}



